"use client";

import { useState } from 'react';
import { suggestCodeStyle, SuggestCodeStyleOutput } from '@/ai/flows/suggest-code-style';
import { Section } from './section';
import { Button } from '@/components/ui/button';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Wand2, Loader2, Lightbulb } from 'lucide-react';

export function AiAdvisorSection() {
  const [codeSnippet, setCodeSnippet] = useState('');
  const [designSystem, setDesignSystem] = useState('Tailwind CSS');
  const [result, setResult] = useState<SuggestCodeStyleOutput | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (!codeSnippet.trim()) {
      setError('Please enter a code snippet.');
      return;
    }
    setError(null);
    setIsLoading(true);
    setResult(null);
    try {
      const output = await suggestCodeStyle({ codeSnippet, designSystem });
      setResult(output);
    } catch (err) {
      console.error(err);
      setError('An error occurred while getting suggestions. Please try again.');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <Section
      id="ai-advisor"
      title="AI Code Style Advisor"
      description="Paste a code snippet and get AI-powered suggestions to align it with popular design systems."
      className="bg-secondary"
    >
      <div className="max-w-4xl mx-auto">
        <Card className="bg-card">
          <CardContent className="p-6">
            <form onSubmit={handleSubmit} className="space-y-6">
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <Textarea
                  placeholder="Paste your React component code here..."
                  value={codeSnippet}
                  onChange={(e) => setCodeSnippet(e.target.value)}
                  className="md:col-span-3 h-48 font-code"
                />
              </div>
              <div className="flex flex-col sm:flex-row gap-4 items-center">
                <Select value={designSystem} onValueChange={setDesignSystem}>
                  <SelectTrigger className="w-full sm:w-[200px]">
                    <SelectValue placeholder="Select Design System" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="Tailwind CSS">Tailwind CSS</SelectItem>
                    <SelectItem value="Material UI">Material UI</SelectItem>
                    <SelectItem value="Bootstrap">Bootstrap</SelectItem>
                    <SelectItem value="Shadcn/UI">Shadcn/UI</SelectItem>
                  </SelectContent>
                </Select>
                <Button type="submit" disabled={isLoading} className="w-full sm:w-auto">
                  {isLoading ? (
                    <>
                      <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                      Analyzing...
                    </>
                  ) : (
                    <>
                      <Wand2 className="mr-2 h-4 w-4" />
                      Get Suggestions
                    </>
                  )}
                </Button>
              </div>
              {error && <p className="text-sm font-medium text-destructive">{error}</p>}
            </form>

            {isLoading && (
              <div className="mt-8 text-center">
                <Loader2 className="mx-auto h-8 w-8 animate-spin text-primary" />
                <p className="mt-2 text-muted-foreground">AI is thinking...</p>
              </div>
            )}

            {result && (
              <div className="mt-8 space-y-6">
                <Card>
                  <CardHeader>
                    <CardTitle className="flex items-center gap-2 font-headline">
                      <Wand2 className="text-primary" />
                      Styled Code Snippet
                    </CardTitle>
                  </CardHeader>
                  <CardContent>
                    <pre className="bg-muted p-4 rounded-md overflow-x-auto">
                      <code className="font-code text-sm">{result.styledCodeSnippet}</code>
                    </pre>
                  </CardContent>
                </Card>
                <Card>
                  <CardHeader>
                    <CardTitle className="flex items-center gap-2 font-headline">
                      <Lightbulb className="text-yellow-400" />
                      Explanation
                    </CardTitle>
                  </CardHeader>
                  <CardContent>
                    <p className="text-muted-foreground whitespace-pre-wrap">{result.explanation}</p>
                  </CardContent>
                </Card>
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </Section>
  );
}
